import React, { Component } from "react";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import Badge from "react-bootstrap/Badge";

class Education extends Component {
  render() {
    if (this.props.resumeEducation && this.props.resumeBasicInfo) {
      var sectionName = this.props.resumeBasicInfo.section_name.education;
      var work = this.props.resumeEducation.map(function (work, i) {
        
        
        var cgpa = work.cgpa
        var degree = work.degree.map((technology, i) => {
          return (
            <Badge  className="main-badge mr-2 mb-2" key={i} style = {{maxWidth:"22rem"}}>
              {technology}
            </Badge>
          );
        });
       var years = <p style = {{fontSize:"16px"}}>{work.years}</p>
        return (
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date={years}
            iconStyle={{
              background: "#AE944F",
              color: "#fff",
              textAlign: "center",
              
              width: "2rem",
                height: "2rem",
                display:"none"
            }}
            
            key={i}
          >
            <div style={{ textAlign: "left", marginBottom: "4px" }}>
              {degree}
            </div>
            <h5
              className="vertical-timeline-element-title"
              style={{ textAlign: "left" }}
            >
            
              {work.additional}
            </h5>
            <h3
              className="vertical-timeline-element-title"
              style={{ textAlign: "left" }}
            >
            
              {work.institute}
            </h3>
            <h4
              className="vertical-timeline-element-subtitle"
              style={{ textAlign: "left" }}
            >
              {work.stream}
            </h4>
            {
                cgpa !== "" ? <div style={{ textAlign: "left", marginTop: "15px", fontSize:"1.5rem" }}>CGPA: {cgpa}</div>:
                <div style={{ textAlign: "left", marginTop: "15px", fontSize:"1.5rem" }}>Percentage: {work.percentage}</div>
            }
            
          </VerticalTimelineElement>
        );
      });
    }

    return (
      <section id="education" className="pb-5">
        <div className="col-md-12 mx-auto">
          <div className="col-md-12">
            <h1 className="section-title" style={{ color: "black" }}>
              <span className="text-black" style={{ textAlign: "center" }}>
                {sectionName}
              </span>
            </h1>
          </div>
        </div>
        <div className="col-md-8 mx-auto">
          <VerticalTimeline>
            {work}
            <VerticalTimelineElement
              iconStyle={{
                background: "#AE944F",
                color: "#fff",
                textAlign: "center",
                display:"none"
              }}
              
            />
          </VerticalTimeline>
        </div>
      </section>
    );
  }
}

export default Education;
